.App {
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(rgba(15, 118, 153, 0), rgba(15, 118, 153, 0)), url('./images/pic-three.jpg');
  background-size: cover;
  background-position: center;
  background-position-y: bottom;
  display: flex;
  justify-content: center;
  
}
